import router from '@/router'
import * as PIXI from 'pixi.js'
import handlers from '@/handlers/Handlers'

export default {
	namespaced: true,

	state: {
		id: '',
		players: [],
		entities: [],
		chatHistory: [],
		pixi: undefined,
		webSocket: undefined
	},

	getters: {
		isInGame: (state) => {
			return !!state.webSocket
		}
	},

	actions: {
		joinGame: async({ state, commit, dispatch }, game) => {
			commit('setId', game.id)
			const webSocket = new WebSocket(process.env.VUE_APP_SERVER_WEBSOCKET + '/play/' + game.id)
			const keepAliveInterval = setInterval(() => {
				webSocket.send(JSON.stringify({
					type: 'system/keepalive'
				}))
			}, 15000)

			webSocket.onopen = (event) => {
				commit('setWebSocket', webSocket)
				webSocket.send(JSON.stringify({
					type: 'get/chat'
				}))
				webSocket.send(JSON.stringify({
					type: 'get/players'
				}))
				webSocket.send(JSON.stringify({
					type: 'get/entities'
				}))
				router.push({ name: 'PlayView' })
			}

			webSocket.onmessage = (event) => {
				const message = JSON.parse(event.data)
				const type = message.type
				const handler = handlers[type]
				if (!handler) {
					console.warn('No handler for ' + message.type)
					return
				}
				handler({ state, commit, dispatch }, message.data)
			}

			webSocket.onclose = (event) => {
				commit('setWebSocket', undefined)
				clearInterval(keepAliveInterval)
			}
		},

		registerPixi: ({ state, commit, dispatch }, pixi) => {
			commit('setPixi', pixi)
			state.entities.forEach(card => dispatch('createCard', card))
		},

		clearPixi: ({ state, commit, dispatch }) => {
			commit('setEntities', [])
			commit('setPixi', undefined)
		},

		createCard: ({ state, commit, dispatch }, card) => {
			commit('addCard', {
				...card,
				loading: true
			})

			if (!state.pixi) {
				return
			}

			const stage = state.pixi.stage
			const loader = new PIXI.Loader()
			loader.add('/client/assets/textures/circle-purple.png')

			loader.load((loader, resources) => {
				const cardRenderer = new PIXI.Sprite(resources['/client/assets/textures/circle-purple.png'].texture)
				stage.addChild(cardRenderer)
				cardRenderer.width = 32
				cardRenderer.height = 32

				commit('updateCard', {
					id: card.id,
					loading: false,
					renderer: cardRenderer
				})
				dispatch('syncCardRenderer', card)
			})
		},

		syncCard: ({ state, commit, dispatch }, cardData) => {
			const card = state.entities.find(card => card.id === cardData.id)
			if (!card) {
				return
			}

			commit('updateCard', cardData)
			dispatch('syncCardRenderer', card)
		},

		syncCardRenderer: ({ state, commit, dispatch }, cardData) => {
			const card = state.entities.find(card => card.id === cardData.id)
			if (!card) {
				return
			}

			const renderer = card.renderer
			if (!renderer && !card.loading) {
				return
			}

			renderer.x = cardData.position.x
			renderer.y = cardData.position.y
		},

		destroyCard: ({ state, commit, dispatch }, targetCard) => {
			console.log(targetCard.id)
			const registeredCard = state.entities.find(card => card.id === targetCard.id)
			if (registeredCard && registeredCard.renderer) {
				state.pixi.stage.removeChild(registeredCard.renderer)
			}
			commit('removeCard', targetCard)
		}
	},

	mutations: {
		setId(state, id) {
			state.id = id
		},

		addPlayer(state, player) {
			state.players.push(player)
		},
		setPlayers(state, players) {
			state.players = players
		},

		addCard(state, card) {
			state.entities.push(card)
		},
		setEntities(state, entities) {
			state.entities = entities
		},
		removeCard(state, card) {
			const cardIndex = state.entities.indexOf(card)
			state.entities.splice(cardIndex, 1)
		},
		updateCard(state, dataFields) {
			const card = state.entities.find(card => card.id === dataFields.id)
			if (!card) { return }

			const keys = Object.keys(dataFields)
			keys.forEach(key => {
				card[key] = dataFields[key]
			})
		},

		addChatEntry(state, entry) {
			state.chatHistory.push(entry)
		},
		setChatHistory(state, chatHistory) {
			state.chatHistory = chatHistory
		},

		setWebSocket(state, webSocket) {
			state.webSocket = webSocket
		},

		setPixi(state, pixi) {
			state.pixi = pixi
		}
	}
}
